:root{
  --bg-main: #ffffff;
  --bg-glass: #ffffff1b;
  --text-sigma: black;
  --text-bright-text: #676767;
  --text-highlight-text: #4f4f4f;
  --border-shin: #c9b8e379;
  --accent-color: #d5c8ff;
  --accent-color-bright: #a58aff;
  --Tool-tip: #c9b8e379;
  --line-color: #c4aee8;
  --Theme-changing-button: "./Assets/Images/Theme-Dark.svg";
  --border-radius-small: 7px;
  --border-radius-medium: 15px;
  --border-radius-large: 20px;
}

[App-theme="dark"]{
  --bg-main: #1B1E2B;
  --bg-glass: #33385135;
  --text-sigma: #d4d4d4;
  --text-bright-text: #b0b3c3;
  --text-highlight-text: #bdbdbd;
  --border-shin: #291f385e;
  --accent-color: #bd92eae0;
  --Tool-tip: #bd92eaa2;
  --line-color: #6b5889;
  --Theme-changing-button: "./Assets/Images/Theme-Light.svg"
}

@import url('https://fonts.googleapis.com/css2?family=Alatsi&family=Nunito:ital,wght@0,200..1000;1,200..1000&display=swap');
@import url('https://db.onlinewebfonts.com/c/77d394fa0c1103c648f880b77cb4a32f?family=Futura+LT+W01+Medium');
/* general */
h1{
  font-family:'Alatsi', 'Xillian', sans-serif;
  font-size: 2.5em;
  text-align: right;
  color: var(--text-highlight-text);
}

/* Components */

/* main body */

.mainBody {
  background-color: var(--bg-main);
  background-image: url("./Assets/Images/BG.png");
  background-size: 15px;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  z-index: 1;
}


/* Toolbar */
.Tools {
  position: -webkit-sticky;
  bottom: 15px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  position: fixed;
  z-index: 1;
}

.Tools > div {
  background: rgba(154, 141, 175, 0.147);
  border-radius: var(--border-radius-medium);
  box-shadow: 4px 4px 10px var(--border-shin);
  backdrop-filter: blur(2px);
  -webkit-backdrop-filter: blur(2px);
  margin: 10px;
  padding: 7px 15px 5px 15px;
}
.theme-button:hover{
  cursor: pointer;
  border: solid 1px var(--accent-color);
  background: #ffffff00;
  backdrop-filter: blur(1px);
  -webkit-backdrop-filter: blur(1px);
  transition: all .5s;
}

.Copy-Actions{
  color: var(--text-highlight-text);
  font-size:1.5em;
  padding: 3px 10px;
  cursor: pointer;
  outline: none;
}

.Copy-Actions:hover{
  color: var(--accent-color-bright);
}

.tool-tip{
  color: var(--text-sigma)!important;
  background-color: var(--Tool-tip)!important;
}


/* ContentBody */
.ContentBody{
  font-family: 'Futura LT W01 Medium', Courier, monospace;
  color: var(--text-sigma);
  letter-spacing: .05cap;
  align-self: center;
  background: var(--bg-glass);
  backdrop-filter: blur(4.5px);
  -webkit-backdrop-filter: blur(4.5px);
  min-width: 420px;
  max-width: 1400px;
  height: fit-content;
  width: 80%;
  height: 90em;
  z-index: 0;
}

/* Header */
.Header{
  width: 100%;
  background-color: var(--bg-main);
  display: flex;
  flex-direction: row;
  padding: 10px 0;
}

.Header > img{
  margin-left: 15px;
  margin-right: auto;
  width: 50px;
}

.Header > .seperators{
  width: 4px;
  height: 2.5rem;
  border-radius: 2px;
  background-color: var(--accent-color);
}

.Header > .SMlinks{
  color: var(--text-highlight-text);
  font-size:larger;
  padding: 10px 10px;
  margin: 0 15px;
  cursor: pointer;
  
}

.SMlinks:hover{
  background-color: var(--accent-color);
  border-radius: var(--border-radius-small);
  color: var(--text-sigma);
}

.UIU-prof{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.2em;
  padding: 10px 10px 7px 10px;
  color: var(--text-highlight-text);
  height: 1.2em;
  margin: 0 15px;
  font-weight: bold;
}

.UIU-prof:hover{
  background-color: var(--accent-color);
  border-radius: var(--border-radius-small);
  color: var(--text-sigma);
}

/* Intro page */
.intro-page{
  display: flex;
  margin: 7rem 4rem 4rem 4rem;
  padding-bottom: 4rem;
  flex-direction: row-reverse;
  border-bottom: dashed 1px var(--line-color);
  flex-wrap: wrap;
}

.hero-image{
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}

.hero-image > img{
  max-width: 500px;
  width: 100%;
}

.intro-details{
  margin-left: auto;
  font-family: 'Futura LT W01 Medium', Courier, monospace;
  /* font-weight: bold; */
  color: var(--text-bright-text);
  text-align: right;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.intro-details > h1{
  margin-bottom: 0em;
}

.intro-details > div{
  font-size: 1.4em;
}


/* About - Section */
.About-page{
  width: 70%;
  text-align: left;
  margin: auto;
  /* background-color: var(--bg-glass); */
  padding: 1em 3em;
  border-radius: var(--border-radius-large);
}

/* Components */


/* @media only screen and (min-width: 768px){

} */

@media only screen and (max-width: 600px){
  /* General */
  h1{
    text-align: center;
  }
  /* Header */
  .Header > img{
    width: 30px;
  }

  .Header > .SMlinks{
    margin: 0 7px;
  }

  .UIU-prof{
    padding: 10px 7px 7px 7px;
  }

  /* Inttro page */
  .intro-page{
    margin: 4rem 1em 2rem 1em;
    padding-bottom: 3em;
  }

  .intro-details{
    text-align: center;
    margin-left: auto;
    margin-right: auto;
  }

}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
